@font-face {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  font-display:swap;
  src: url(assets/fonts/Poppins.eot);
  src: local('Poppins'), url(assets/fonts/Poppins.eot?#iefix) format('embedded-opentype'), url(assets/fonts/Poppins.svg#Poppins) format('svg');
}

@font-face {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 500;
  font-display:swap;
  src: url(assets/fonts/Poppins-normal-500.woff) format('woff'), url(assets/fonts/Poppins-normal-500.ttf) format('truetype'), url(assets/fonts/Poppins-normal-500.woff2) format('woff2');
}

@font-face {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 600;
  font-display:swap;
  src: url(assets/fonts/Poppins-normal-600.woff) format('woff'), url(assets/fonts/Poppins-normal-600.ttf) format('truetype'), url(assets/fonts/Poppins-normal-600.woff2) format('woff2');
}

